.button {
    padding: 8px 16px;
    margin: 0 16px 16px 0;
    border: none;
    border-radius: 4px;
    text-decoration: none;
    color: #FFF;
    font-size: 16px;
    background-color: rgb(250, 97, 41);
    cursor: pointer;

    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.button:hover {
    font-weight: bold;
    background-color: orangered;
}